import React, {Component} from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Link
} from 'react-router-dom'
import Drawer from './components/Layout/Drawer'
import Dashboard from './views/Dashboard'
import MyObjectives from './views/MyObjectives'
import NewObjective from './views/NewObjective'
import DeleteObjectives from './views/DeleteObjectives'

class App extends Component {
  
  render() {

    return (   
        <Router>
          <Drawer>          
            <Switch>
              <Route exact path="/" component={Dashboard}/>
              <Route path="/myobjectives" component={MyObjectives}/>
              <Route path="/newobjectives" component={NewObjective}/>
              <Route path="/deleteobjectives" component={DeleteObjectives}/>
            </Switch>    
          </Drawer>
        </Router>
    )
  }
}

export default App;
