import React, { Component } from 'react'
import Grid from '@material-ui/core/Grid'
import axios from 'axios'
import Objective from '../components/Objective/Objective'

export default class Dashboard extends Component {
	constructor(props) {
		super(props)
		this.state={
		  objectives:[],
		  keyobjectives:[]
		}
	}

	componentDidMount() {
		this.getData()

		setInterval(this.getData, 5000)
	}

	getData = () => {
		axios.get('/obj')
		.then(response => {
			this.setState({objectives: response.data.recordset})
		})

		axios.get('/keyobj')
		.then(res => {
			this.setState({keyobjectives: res.data.recordset})
		})
	}

	findKeyObjectives(id) {
		return this.state.keyobjectives.filter((keyobjective) => {
			return keyobjective.id == id
		})
	} 

	makeObjective() {
		return this.state.objectives.map((objective) => (
			<Objective objectivename={objective.name} supervisor={objective.supervisor} progress={objective.progress} keyobjectives={this.findKeyObjectives(objective.id)}/>
		))
	}

	render() {
		return(
			<>
				<div>
					<Grid 
					container direction="row" 
					justify="space-evenly"
  					alignItems="flex-start">
						{this.makeObjective()}
					</Grid>
				</div>
			</>
		)
	}
}
